import {
	request
} from "@/util/request.js";

/**
 * 
 * :'URL' // 
 */
const API = {
	token: `system/login`, 
	register: `system/admin/add`,
	delImg:`common/album/albumDel`, //
	getUserInfo: `system/admin/self`, // 
};

export function token(pramter) {
	return request({
		url: API.token,
		data: pramter,
		method: "POST",
	});
}

export function register(pramter) {
	return request({
		url: API.register,
		data: pramter,
		method: "POST",
	});
}

export function delImg(pramter) {
	return request({
		url: API.delImg,
		data: pramter,
		method: "POST",
	});
}

export function getUserInfo(pramter) {
	return request({
		url: API.getUserInfo,
		data: pramter,
		method: "GET",
	});
}