import {
	request
} from "@/util/request.js";

/**
 * 
 * :'URL' // 
 */
const API = {
	newsList: `article/news/list`, // 
	website: `setting/website/detail`, // 
	protocol:`setting/protocol/detail`, // 
};

// 
export function newsList(pramter) {
	return request({
		url: API.newsList,
		data: pramter,
		method: "GET",
	});
}

// 
export function weaponList(pramter) {
	return request({
		url: API.weaponList,
		data: pramter,
		method: "GET",
	});
}

// 
export function heroList(pramter) {
	return request({
		url: API.heroList,
		data: pramter,
		method: "GET",
	});
}

// 
export function website(pramter) {
	return request({
		url: API.website,
		data: pramter,
		method: "GET",
	});
}

// 
export function mapList(pramter) {
	return request({
		url: API.mapList,
		data: pramter,
		method: "GET",
	});
}


// 
export function protocol(pramter) {
	return request({
		url: API.protocol,
		data: pramter,
		method: "GET",
	});
}



